<template>
  <div class="header-wrapper">
    <div class="header">
      <div class="logo"><img src="http://res.yujingceping.com/res/resources/front/imgs/Logo.png" alt=""></div>
      <div class="user-info">
        <el-dropdown trigger="click" @command="handleCommand">
                  <span class="el-dropdown-link">
                      <img class="user-logo" src="../../resources/front/imgs/common/cat.png">
                      {{username}}
                  </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="loginout">退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        name: '译泰'
      }
    },
    computed:{
      username(){
        let username = JSON.parse(localStorage.getItem('userInfo')).name;
        return username ? username : this.name;
      }
    },
    methods:{
      handleCommand(command) {
        if(command == 'loginout'){
          let $this = this;
          this.$ajax('logout',
          ).then(function (response) {
            console.log(response);
            if (response.code == 200){
              localStorage.removeItem('userInfo')
              $this.$router.push('/login');
            }
          }).catch(function (error) {
            console.log(error);
          });

        }
      }
    }
  }
</script>
<style scoped>
  .header-wrapper {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 70px;
    font-size: 22px;
    line-height: 70px;
    color: black;
    background: #fff;
  }
  .header {
    width: 1024px;
    margin: 0 auto;
  }
  .header .logo{
    float: left;
    width:250px;
    text-align: center;
  }
  .header .logo img {
    width: 150px;
    height: 38px;
    margin-top: 21px;
    float: left;
    margin-right: 57px;
  }
  .user-info {
    float: right;
    padding-right: 50px;
    font-size: 16px;
    color: black;
  }
  .user-info .el-dropdown-link{
    position: relative;
    display: inline-block;
    padding-left: 50px;
    color: black;
    cursor: pointer;
    vertical-align: middle;
  }
  .user-info .user-logo{
    position: absolute;
    left:0;
    top:15px;
    width:40px;
    height:40px;
    border-radius: 50%;
  }
  .el-dropdown-menu__item{
    text-align: center;
  }
  img {

  }
</style>



